.offices-page {
	.select {
		margin-bottom: 32px;

		@include mq(md) {
			margin-bottom: 28px;
		}
	}

	&__links {
		@include flex-simple(16px 24px);

		flex-wrap: wrap;
		margin-bottom: 24px;

		@include mq(md) {
			margin-bottom: 32px;
		}
	}

	&__buttons {
		@include flex-simple(8px);

		flex-wrap: wrap;
		margin-bottom: 32px;
	}

	&__button {
		border-color: var(--color-red-500);

		&--active {
			color: var(--color-red-500);
			background-color: var(--color-red-100);
		}
	}

	&__map {
		width: 100%;
		height: 415px;

		iframe,
		div {
			width: 100%;
			height: 100%;
		}

		iframe {
			border: 0;
			border-radius: 4px;
		}

		.marker {
			@include bg-properties(contain);

			width: 40px;
			height: 40px;
			background-image: url('../img/icons/pin.svg');

			&--hidden {
				display: none;
			}
		}
	}
}
